    // 
    fillStackPiles() {
        let thisObj = this;

        let fill = function(index: number) {
            let pile = thisObj.createPile();
            thisObj._mapStack.addChild(pile);
            pile.active = true;
            let pos = pile.getPosition()
            pos.x = thisObj._stackPilePosXArr[index] + 750;
            pile.setPosition(pos);
        
            tween(pile)
                .delay(0.36 * index)
                .by(0.3, {
                    position: v3(-750, 0, 0),
                })
                .call(function() {
                    audioManager.playEffect(constants.audioNames.pileIn);
                })
                .to(0.06, {
                    scale: v3(1.1, 1.1, 1.1)
                })
                .to(0.14, {
                    scale: v3(1, 1, 1)
                })
                .call(function() {
                    let pos = pile.getPosition()
                    pos.x = thisObj._stackPilePosXArr[index];
                    pile.setPosition(pos);
                    
                    pile.canTouch = true;
                    pile.originalPos = pos;
                })
                .start();
        }

        for (let i = 0; i < 3; i++) {
            fill(i);
        }
    }

    getAreaByWorldPos(pos: Vec3) {
        let target: Node = null;
        utilTools.forArr(this._mapArea.children, function(area: Node, index: number) {
            if (area.active && cocosUtil.convertToWorldSpace(area.parent, area.getPosition()).equals(pos, 62)) {
                target = area;
                return true;
            }

            return false;
        });

        return target;
    }

